.b-subscribe{
    height: 130px;
	padding: 43px 0 47px;
	background-color: #0f0f0f;
    
    @include huge {
        height: 95px;
        padding: 47px 0 47px;
    }
    @include pc {
        height: auto;
    }
    @include tablet{
        padding: 40px 0 45px;
    }
    @include small{
        margin: 0 auto;
        padding: 22px 0 39px;
        width: calc(100% - 30px);
    }
    
    .container{
        height: 100%;
    }
    
}
.b-subscribe__title{
//	display: inline-block;
	margin: 0 29px 0 0;
	font-size: 28px;
//	line-height: 40px;
	line-height: normal;
	font-weight: 700;
	text-transform: uppercase;
	color: #fff;
	vertical-align: middle;

//	@include twoK {
//		font-size: 30px;
//	}
//	@include fourK {
//		font-size: 40px;
//		line-height: 40px;
//		padding-top: 10px;
//	}
	@include huge {
        margin: 0 20px 0 0;
		font-size: 20.39px;
//		text-transform: none;
	}
    @include pc{
        margin: 0 0 35px;
        width: 100%;
        text-align: center;
//        text-transform: uppercase;
        font-size: 21px;
    }
    @include small{
        margin-bottom: 25px;
        font-size: 18px;
        line-height: 22px;
    }
    
    br{
        display: none;
        
        @include small{
            display: inline;
        }
    }
}
.b-subscribe__form{
//	display: inline-block;
	vertical-align: middle;
    display: flex;
//    justify-content: space-between;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    padding: 0 16px;
    
    @include huge {
        padding: 0 12px;
    }
    @include pc{
        padding: 0;
        flex-wrap: nowrap;
        flex-direction: column;
//        font-size: 14px;
    }
    @include note{
//        flex-direction: column;
    }
    @include tablet{
        font-size: 12px;
    }
    @include small{
        padding: 0 12px;
    }
    
	input {
//		width: auto;
		width: 310px;
        min-height: 40px;
		box-sizing: border-box;
		display: inline-block;
		margin-right: 29px;
		font-size: 16px;
		padding: 4px 17px;

//		@include twoK {
//			font-size: 21px;
//		}
//		@include fourK {
//			font-size: 33px;
//			line-height: 33px;
//			min-width: 524px;
//		}
		@include huge {
            margin-right: 20px;
            width: 226px;
//			min-width: 235px;
            min-height: 29px;
            padding: 4px 12px;
            font-size: 11.65px;
		}
        @include pc{
            width: 300px;
            min-height: 35px;
            margin-right: 0;
            font-size: 14px;
            margin-bottom: 33px;
        }
        @include note{
        }
        @include small{
            width: 263px;
            margin-bottom: 27px;
            font-size: 12px;
            min-height: 31px;
            padding: 2px 14px;
        }

		&:last-child{
			margin-right: 0;
		}
        
        &:hover{
//	        border:none;
        }
	}
	input[type="email"]{
//		padding-top: 10px;
//		padding-bottom: 10px;
		width: 320px;
//		margin-left: -4px;
//		margin-right: 26px;
        
        @include huge{
//            width: auto;
            width: 233px;
        }
        @include pc{
            width: 300px;
        }
        @include small{
            width: 263px;
        }
	}
    input[type="email"],
	input[type="text"], 
    input[type="tel"],{
		border: none;
	}
	input[type="submit"] {
//		min-width: 0;
		width: 137px;
		padding: 9px 13px 7px;
		font-weight: 500;
		font-size: 14px;
        letter-spacing: .016em;
//		@include fourK {
//			line-height: 40px;
//		}
        @include huge{
            width: auto;
            min-width: 0;
            padding: 1px 10px 0;
            font-size: 10.2px;
        }
        @include pc{
            margin-bottom: 0;
            width: 155px;
            font-size: 14px;
        }
        @include small{
            width: auto;
            font-size: 12px;
            line-height: 23px;
            padding: 2px 13px;
            min-width: initial;
        }
        
        &:hover{
            border: 2px solid transparent;
        }
	}
}

.b-subscribe__before{
    @extend .b-subscribe__after;
    transform: rotate(180deg);
}
.b-subscribe__after{
    height: 535px;
//    background: #fff url(../img/flowers/flowers-subscribe-lg.jpg) no-repeat top;
//    background: #fff url(../img/flowers/flowers-subscribe-lg.jpg) no-repeat top/1530px;
    background-color: #fff;
    background-repeat: no-repeat;
    background-position: top;
    background-size: 1370px;
//    background: #fff url(../img/flowers/flowers-subscribe-xl-1X.jpg) no-repeat top/1370px;
    background-image: url(../img/flowers/flowers-subscribe-xl-1X.jpg);
    
    @include media("retina2x"){
//        background: #fff url(../img/flowers/flowers-subscribe-lg-2x.jpg) no-repeat center -8px/79.4%;
//        background: #fff url(../img/flowers/flowers-subscribe-xl-2X.jpg) no-repeat top/1370px;
        background-image: url(../img/flowers/flowers-subscribe-xl-2X.jpg);
    }
    
    @include huge{
        height: 389px;
//        background-size: 1116px;
        background-size: 1000px;
        background-image: url(../img/flowers/flowers-subscribe-lg-1X.jpg);
        
        @include media("retina2x"){
            background-image: url(../img/flowers/flowers-subscribe-lg-2X.jpg);
        }
    }
    @include pc{
//        background-size: 1065px;
        background-size: 950px;
        background-image: url(../img/flowers/flowers-subscribe-ml-1X.jpg);
        
        @include media("retina2x"){
            background-image: url(../img/flowers/flowers-subscribe-ml-2X.jpg);
        }
    }
    @include note{
//        height: 505px;
//        background: #fff url(../img/flowers-subscribe.jpg) top no-repeat /contain;
//        background: #fff url(../img/flowers/flowers-subscribe-md.jpg) no-repeat top/765px;
        background-size: 690px;
        background-image: url(../img/flowers/flowers-subscribe-md-1X.jpg);
        
        @include media("retina2x"){
//            background: #fff url(../img/flowers/flowers-subscribe-md-2x.jpg) no-repeat top /88%;
            background-image: url(../img/flowers/flowers-subscribe-md-2X.jpg);
        }
