.b-why-we__title{
	margin: 0 0 14px;
	font-size: 26px;
	font-weight: 700;
	line-height: 30px;
    letter-spacing: -.01em;
	text-transform: uppercase;
	color: #121212;

//	@include fourK {
//		font-size: 35px;
//		line-height: 40px;
//	}
//	@include twoK {
//		font-size: 30px;
//		line-height: 30px;
//	}
	@include huge {
		font-size: 18.94px;
		line-height: 24px;
		margin-bottom: 7px;
	} 
    @include small{
        letter-spacing: -0.02em;
        font-size: 16px;
        line-height: 20px;
//        margin-bottom: 7px;
    }
}
.b-why-we__text{
	margin: 0;
    width: 370px;
//	padding: 0;
//	padding-right: 0;
	padding-left: 1px;
	font-size: 16px;
	font-weight: 400;
	line-height: 18px;
	color: #121212;

//	@include twoK {
//		font-size: 20px;
//		line-height: 20px;
//	}
//	@include fourK {
//		font-size: 28px;
//		line-height: 35px;
//	}
	@include huge {
        width: 265px;
        line-height: 14px;
		font-size: 11.65px;
	}
	@include pc {
        width: auto;
        line-height: 18px;
		font-size: 14px;
	}
    @include small{
        padding-left: 0;
        line-height: 13px;
        font-size: 11.34px;
    }
}
.b-why-we__icon{
	position: absolute;
//	top: 0; left: 0;
    img{
        width: 100%;
            transform: scale(1);
//        transform: scale(0.9);
            
        @include huge{
            transform: scale(1);
        }   
        @include pc{
            transform: scale(0.9);
        }
      @include small{
        transform: scale(1);
      }
    }
}
